<template>
  <div class="personal">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="80px">
      <el-form-item label="" prop="name">
        <el-input v-model="ruleForm.name" disabled></el-input>
      </el-form-item>
      <el-form-item label="" prop="sex">
        <el-select v-model="ruleForm.sex" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input type="email" v-model="ruleForm.email"></el-input>
      </el-form-item>
      <el-form-item label="" prop="college">
        <el-select v-model="ruleForm.college" placeholder="">
          <el-option v-for='(item,index) in college' :key="index" :label="item.name" :value="item.name"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input type="password" v-model="ruleForm.password"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import  store from '@/store/admin/teacher'
  import college from '../../college.js'
     export default {
            name:"index",
            data() {
                return {
                    college:college,
                    ruleForm: {
                            _id:JSON.parse(this.$route.query.login_info).recodeset._id,
                            name: JSON.parse(this.$route.query.login_info).recodeset.username,
                            sex: '',
                            college:JSON.parse(this.$route.query.login_info).recodeset.college,
                            status: 0,
                            password: '',
                            email: JSON.parse(this.$route.query.login_info).recodeset.email,
                            },
                     rules: {
                            name: [
                                { required: true, message: '', trigger: 'blur' },
                                { min: 2, max: 5, message: ' 3  5 ', trigger: 'blur' }
                            ],
                            sex: [
                                { required: true, message: '', trigger: 'change' }
                            ],
                            college: [
                                { required: true, message: '', trigger: 'change' }
                            ],
                            email: [
                                { required: true, message: '', trigger: 'change' },
                                 { type: 'email', message: '', trigger: ['blur', 'change'] }
                            ],
                            password: [
                                { required: true, message: '', trigger: 'change' }
                            ]
                            }
                }
            },
            methods:{
                submitForm(formName) {
                    let that = this;
                    this.$refs[formName].validate((valid) => {
                    if (valid) {
                        console.log(JSON.parse(this.$route.query.login_info));
                        that.ruleForm._id = JSON.parse(this.$route.query.login_info).recodeset._id;
                        store.doAdd(that.ruleForm).then((r)=>
                        {
                            if(r.data.status == 1)
                            {
                                this.$message({
                                            type:"success",
                                            message:""
                                        })
                                this.$router.push({
                                    path:"/teacher"
                                })
                            }else 
                            {
                                this.$message(
                                    {
                                        type:"error",
                                        message:r.data.message
                                    }
                                )
                            }
                        })
                    } else {
                        this.$message(
                            {
                                type:"error",
                                message:""
                            }
                        )
                        return false;
                    }
                    });
                }
         }
}
</script>
<style scoped>


</style>



